.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--clr-semi-dark-navy);
  width: 100%;
  z-index: 20;
}
